<template>
  <div class="asset-account">
    <div class="auto-box">
      <div class="top">
        <div class="total">{{ $t('assets.top.text1') }}<span class="highlight">{{ assetTotal }} BTC</span> <span class="small">≈ {{ assetPrice }} CNY</span> </div>
        <div class="parts">
          <span class="part-1">
            {{ $t('assets.top.text2') }}<span class="highlight">{{ ctcTotal }} BTC</span> ≈ {{ ctcPrice }} CNY
          </span>
          <span class="part-2">
            {{ $t('assets.top.text3') }}<span class="highlight">{{ otcTotal }} BTC</span> ≈ {{ otcPrice }} CNY
          </span>
        </div>
        <div class="desc">{{ $t('assets.top.text4') }}</div>
      </div>
      <div class="bottom">
        <el-tabs v-model="activeName" @tab-click="handleTabClick">
          <el-tab-pane :label="$t('assets.bottom.tab1')" name="ctc">
            <ctc-asset-table 
              v-loading="loading"
              :data="ctcListData"
              :currentSwitch="currentSwitch"
              @refresh="handleRefresh"></ctc-asset-table>
          </el-tab-pane>
          <el-tab-pane :label="$t('assets.bottom.tab2')" name="otc">
            <otc-asset-table
              v-loading="loading"
              :data="otcListData"
              :currentSwitch="currentSwitch"
              @refresh="handleRefresh"></otc-asset-table>
          </el-tab-pane>
        </el-tabs>
        
      </div>
    </div>
  </div>
</template>

<script>
  import { getCtcFinancialCenter, getOtcFinancialCenter } from '@/api/assets/index'
  import CtcAssetTable from './components/CtcAssetTable'
  import OtcAssetTable from './components/OtcAssetTable'
  export default {
    name: 'asset-account',
    components: {
      CtcAssetTable,
      OtcAssetTable
    },
    data() {
      return {
        loading: false,
        assetTotal: null,
        assetPrice: null,
        ctcTotal: null,
        ctcPrice: null,
        otcTotal: null,
        otcPrice: null,
        activeName: this.$route.query.tab ? this.$route.query.tab : 'ctc',
        ctcListData: [],
        otcListData: [],
        currentSwitch: 1,
        refreshType: ''
      }
    },
    watch: {
      $route(val) {
        this.activeName = val.query.tab === 'ctc' ? 'ctc' : 'otc'
        this.getInfo()
      }
    },
    mounted() {
      this.getInfo()
    },
    methods: {
      handleRefresh(val) {
        console.log('refresh-type', val)
        this.refreshType = val
        // 如果没有刷新类型就默认查询币币账户
        if (this.refreshType === 'otc') {
          this.getOtc()
        } else {
          this.getCtc()
        }
      },
      getInfo() {
        // 如果没有query.tab就默认查询币币账户
        if (this.$route.query.tab === 'otc') {
          this.getOtc()
        } else {
          this.getCtc()
        }
      },
      getCtc() {
        this.loading = true
        getCtcFinancialCenter().then(res => {
          this.loading = false
          this.ctcTotal = res.data.convert_price
          this.ctcPrice = res.data.convert_cny
          this.otcTotal = res.data.otc_convert_price
          this.otcPrice = res.data.otc_convert_cny
          this.assetTotal = res.data.all_convert_price
          this.assetPrice = res.data.all_convert_cny
          this.ctcListData = res.data.normal_currency
        }).catch(() => {
  
        })
      },
      getOtc() {
        this.loading = true
        getOtcFinancialCenter().then(res => {
          this.loading = false
          this.otcTotal = res.data.convert_price
          this.otcPrice = res.data.price_cny
          this.ctcTotal = res.data.ctc_convert_price
          this.ctcPrice = res.data.ctc_price_cny
          this.assetTotal = res.data.all_convert_price
          this.assetPrice = res.data.all_convert_cny
          this.otcListData = res.data.list
        }).catch(() => {
  
        })
      },
      handleTabClick(tab, event) {
        this.$router.replace({ path: this.$route.path, query: { tab: tab.name }})
        this.currentSwitch++
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .asset-account{
    .auto-box{
      width: 1200px;
      margin: 0 auto;
      padding: 20px 0 260px;
      .top{
        background-color: #fff;
        padding: 40px 24px 50px;
        line-height: 1;
        .total{
          font-size: 26px;
          color: #333333;
          font-weight: bold;
          .highlight{
            color: #1fa8f6;
          }
          .small{
            font-size: 16px;
            font-weight: normal;
          }
        }
        .parts{
          font-size: 14px;
          color: #333333;
          margin: 30px 0 10px;
          .highlight{
            color: #1fa8f6;
          }
          .part-2{
            margin-left: 10px;
          }
        }
        .desc{
          font-size: 14px;
          color: #7b7e7f;
        }
      }
      .bottom{
        background-color: #fff;
        margin-top: 10px;
        padding: 30px 30px 0;
      }
    }
    /deep/.el-tabs__nav-wrap::after{
      display: none;
    }
    /deep/.el-tabs__header{
      margin: 0px;
    }
  }
</style>



